.wrap {
  height: 100vh;
  width: 100vw;
  position: relative;
}

.App {
  position: relative;
  z-index: 2;
  height: 100vh;
  width: 100vw;
  text-align: center;
  display: flex;
  flex-direction: column;
  cursor: none !important;
}

.cursor {
  position: absolute;
  width: 1px;
  height: 1px;
  pointer-events: none;
}

.cursor_img {
  width: 32px;
  height: 32px;
  margin-top: -8px;
  margin-left: -24px;
  transform: scaleX(-100%);
}

.header {
  padding: 6px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: scaleX(-100%);
}

ul {
  display: flex;
  padding: 0;
  align-items: center;
}

li {
  padding: 8px 8px;
  list-style: none;
}

img.menu {
  height: 24px;
  padding: 0 8px;
}

.login {
  padding: 8px 16px;
  height: 36px;
  font-size: 14px;
  color: #fff;
  background-color: #1a73e8;
  border-radius: 4px;
  border: none;
  margin: 0 8px;
  cursor: none;
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 24px;
}

.google {
  width: 270px;
  transform: scaleX(-100%);
}

.search_bar {
  width: 80%;
  height: 44px;
  border: 1px solid;
  border-color: #dfe1e5;
  border-radius: 100px;
  margin-top: 24px;
  max-width: 600px;
  background-color: rgba(255, 255, 255, 0.8);
}

.search_bar:hover {
  border-color: rgba(100, 100, 100, 0.5);
}

.button_section {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  transform: scaleX(-100%);
}

.button {
  padding: 8px 16px;
  height: 36px;
  font-size: 14px;
  color: #3c4043;
  background-color: rgba(231, 231, 231, 0.828);
  border-radius: 4px;
  border: none;
  margin: 0 8px;
  cursor: none;
}

.button:hover {
  background-color: rgba(243, 243, 243, 0.828);
}

.footer {
  color: #70757a;
  background-color: rgba(231, 231, 231, 0.828);
  padding: 16px 32px;
  text-align: left;
  transform: scaleX(-100%);
}